@mixin flexColumn {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center; }
html,body {
    height: 100vh;
    margin: 0; }
.material-icons {
    font-family: "Material Icons";
    font-weight: normal;
    font-style: normal;
    letter-spacing: normal;
    text-transform: none;
    display: inline-block;
    white-space: nowrap;
    word-wrap: normal;
    direction: ltr;
    -moz-font-feature-settings: "liga";
    text-rendering: optimizeLegibility;
    &::before {
        box-sizing: inherit; } }
canvas {
    width: 100vw;
    height: 100vh;
    display: block;
    background: black; }

#game-overlay {
    position: absolute;
    > button {
        position: relative;
        top: 2rem;
        left: 2rem; } }
#turn_time {
    position: absolute;
    top: 50vh;
    right: 2rem;
    > p {
        color: white;
        margin-bottom: 0;
        text-align: center; } }
.player-self {
    position: absolute;
    bottom: 0;
    color: white;
    right: 1rem; }
.player-opponent {
    position: absolute;
    top: 0;
    color: white;
    right: 1rem; }

.active {
