#side-nav {
    display: flex;
    flex-direction: column;
    position: absolute;
    right: 0;
    height: 100vh;
    width: 14rem;
    background: #43425D;
    > div:hover {
        background: rgba(0,0,0,0.10); }
    > div {
        color: white;
        display: flex;
        justify-content: left;
        justify-items: center;
        align-content: center;
        align-items: center;
        height: 3rem;
        > span {
            user-select: none;
            font-family: "Source Sans Pro"; }
        > i.material-icons {
            margin-left: 1rem;
            margin-right: 0.5rem;
            color: #a5a4bf; } }
    > header {
        display: flex;
        justify-content: left;
        justify-items: center;
        align-content: center;
        align-items: center;
        height: 5rem;
        background: rgba(0,0,0,0.10);
        box-shadow: 0 2px 3px rgba(0,0,0,0.05);
        > h5 {
