#editor {
    overflow: hidden;
    display: flex;
    height: 100vh;
    > div#card-viewer {
        width: 50vw;
        > canvas {
            width: 50vw;
            background: #F1F8FE; }
        > div.so-card.so-card-shadow {
                position: relative;
                z-index: 5;
                bottom: 12rem;
                width: 20%;
                left: 35vw; }

        > button {
            color: #43425D;
            border-color: #43425D;
            position: absolute;
            top: 1rem;
            left: 1rem; } }
    > div#card-selection {
        display: flex;
        height: 100vh;
        width: 50vw;
        > section#finder {
            width: calc(100vw - 50vw - 5rem);
            height: 100vh;
            > nav {
                display: flex;
                >div.so-input.so-select {
                    width: 100%; } }
            > section {
                max-height: 100vh;
                overflow-y: scroll;
                scrollbar-width: thin;
                display: grid;
                gab: 1rem;
                grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
                grid-auto-rows: 180px;
                > div.so-card {
                    > h3 {
                        text-align: left;
                        font-weight: bold; }
                    > div {
                        position: relative;
                        top: 2rem;
                        right: 1rem;
                        display: flex;
                        justify-content: center;
                        align-content: center;
                        align-items: center;
                        justify-items: center;
                        > button {
                            color: #6000EA;
                            margin: 0; }

                        > span {
                            display: flex;
                            font-weight: bold;
                            > svg {
                                margin-right: 4px;
                                > g > path {
                                    fill: #6000EA; } } } }
                    padding-left: 20px;
                    font-family: Arial;
                    color: #6000EA;
                    margin: 6px;
                    width: 15rem; } } }
        > div#deck {
            height: 100vh;
            width: 13rem;
            background: #43425D;
            > h2 {
                text-align:  center;
                color: white; }
            > section {
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-content: center;
                align-items: center;
                position: relative;
                right: -1rem;
                scrollbar-width: thin;
                overflow-y: scroll;
                margin-top: 2rem;
                height: calc(100vh - 15vh);
                width: 90%;
                background: white;
                > div.so-tag.so-tag-default {
                    margin: 5px 0;
                    >div.so-tag-inline>span {
                        margin-left: 5px; } } } } } }
#account {
    display: flex;
    height: 100vh;
    > section {
        width: calc(100vw - 14rem);
        height: 100vh; }
    > section#account-login {
        display: flex;
        justify-content: center;
        align-content: center;
        align-items: center; }
    > section.load-account {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        align-items: center;
        justify-items: center; }
    > section.view-account {
        display: flex;
        justify-content: center;
        justify-items: center;
        align-content: center;
        align-items: center;
        > div.so-image.so-image-circle {}
        > div#details {
            display: flex;
            flex-direction: column;
            > hr {
                width: 100%; }
            > button {
                align-self: center; }
            padding: 5rem; } } }
#settings {
    display: flex;
    height: 100vh;
    > div#settings-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        width: calc(100vw - 14rem);
        > form {
            margin: 0 5rem; } } }
#main-menu {
    display: flex;
    height: 100vh; }

.animated-grid {
  height: 95vh;
  padding: 2rem;
  width: calc(100vw - 14rem);
  display: grid;
  gap: 1rem;
  margin: 0;

  /* Explicit grid */
  grid-template-areas: 'a  b  c  d' 'l  🌟 🌟 e' 'k  🌟 🌟 f' 'j  i  h  g';

  grid-template-rows: repeat(4, 25%);
  grid-template-columns: 240px auto auto 240px;

  --stagger-delay: 100ms; }

@keyframes cardEntrance {
  from {
    opacity: 0;
    transform: scale(0.3);
    filter: hue-rotate(180deg); }

  to {
    opacity: 1;
    transform: scale(1);
    filter: hue-rotate(0deg); } }

.card {
  background-color: #43425D;
  animation: cardEntrance 700ms ease-out;
  animation-fill-mode: backwards;
  user-select: none;
  &:hover {
      transform: scale(1.1); } }
.card:nth-child(1) {
        grid-area: a;
        animation-delay: calc(1 * var(--stagger-delay)); }
.card:nth-child(2) {
        grid-area: b;
        animation-delay: calc(2 * var(--stagger-delay)); }
.card:nth-child(3) {
        grid-area: c;
        animation-delay: calc(3 * var(--stagger-delay)); }
.card:nth-child(4) {
        grid-area: d;
        animation-delay: calc(4 * var(--stagger-delay)); }
.card:nth-child(5) {
        grid-area: e;
        animation-delay: calc(5 * var(--stagger-delay)); }
.card:nth-child(6) {
        grid-area: f;
        animation-delay: calc(6 * var(--stagger-delay)); }
.card:nth-child(7) {
        grid-area: g;
        animation-delay: calc(7 * var(--stagger-delay)); }
.card:nth-child(8) {
        grid-area: h;
        animation-delay: calc(8 * var(--stagger-delay)); }
.card:nth-child(9) {
        grid-area: i;
        animation-delay: calc(9 * var(--stagger-delay)); }
.card:nth-child(10) {
        grid-area: j;
        animation-delay: calc(10 * var(--stagger-delay)); }
.card:nth-child(11) {
        grid-area: k;
        animation-delay: calc(11 * var(--stagger-delay)); }
.card:nth-child(12) {
        grid-area: l;
        animation-delay: calc(12 * var(--stagger-delay)); }
.card:last-child {
        grid-area: 🌟;
        animation-delay: calc(13 * var(--stagger-delay));
        background-image: url("./a.jpg");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center; }




